.registration-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
  align-items: center;
  margin-top: 200px;
  margin-left: 550px;
}

.registration-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.registration-container form {
  display: flex;
  flex-direction: column;
}

.registration-container form > div {
  margin-bottom: 15px;
}

.registration-container label {
  font-weight: bold;
}

.registration-container input {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.registration-container button {
  padding: 10px;
  border-radius: 5px;
  border: none;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
}

.registration-container button:hover {
  background-color: #0056b3;
}

.registration-container h4 {
  text-align: center;
  margin-top: 20px;
}

.registration-container h4 a {
  color: #007bff;
  text-decoration: none;
}

.registration-container h4 a:hover {
  text-decoration: underline;
}
